body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  height: 100%;
}

.Hero-screen-padding{
  flex: 1 1;
}

.App-logo {
  height: 8vmin;
  margin-bottom: 5vmin;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  -webkit-animation: Logo-fly-up 3s;
          animation: Logo-fly-up 3s;
}

.App-logo-wide {
  height: 8vmin;
  -webkit-animation: Fade-in 5s;
          animation: Fade-in 5s;
}

.Hero-screen {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.Full-page-container{
  min-height: 100vh;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  background: -webkit-linear-gradient(#0F0929,#27213E);
  background: linear-gradient(#0F0929,#27213E);
}

.Full-page-container-flat{
  min-height: 100vh;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  background: #27213e;
}

.Particles-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  background: -webkit-radial-gradient(#170E40 1%,#0F0929 40%);
  background: radial-gradient(#170E40 1%,#0F0929 40%);
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
  padding-bottom: 6vmin;
}

.Particles {
  position: absolute;
  width: 100%;
  height: 100vh;
  z-index: -1;
}

.Down-arrow-container{
  align-self: center;
  margin-bottom: 6vmin;
  -webkit-animation: Fade-in 8s;
          animation: Fade-in 8s;
  cursor: pointer;
}

.Down-arrow-container-mobile{
  align-self: center;
  margin-bottom: calc(6vmin + 10vh);
  -webkit-animation: Fade-in 8s;
          animation: Fade-in 8s;
  cursor: pointer;
}

.Down-arrow{
  height: 6vmin;
  -webkit-animation: Down-arrow-bounce infinite 1.5s linear;
          animation: Down-arrow-bounce infinite 1.5s linear;
}

.Sub-header {
  font-family: "Open Sans Light",serif;
  font-weight: 300;
  color:white;
  -webkit-animation: Fade-in 7s;
          animation: Fade-in 7s;
  -webkit-flex-grow: 1;
}

.Card-grid{
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

@-webkit-keyframes Down-arrow-bounce {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  80% {
    -webkit-transform: translateY(2vmin);
            transform: translateY(2vmin);
  }
  100%{
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes Down-arrow-bounce {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  80% {
    -webkit-transform: translateY(2vmin);
            transform: translateY(2vmin);
  }
  100%{
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@-webkit-keyframes Logo-fly-up {
  0% {
    -webkit-transform: translateY(10vmin) scale(0.5,0.5);
            transform: translateY(10vmin) scale(0.5,0.5);
  }

  25% {
    -webkit-transform: translateY(10vmin) scale(3,3);
            transform: translateY(10vmin) scale(3,3);
  }

  50% {
    -webkit-transform: translateY(10vmin) scale(3,3);
            transform: translateY(10vmin) scale(3,3);
  }

  100% {
    -webkit-transform: translateY(0) scale(1,1);
            transform: translateY(0) scale(1,1);
  }
}

@keyframes Logo-fly-up {
  0% {
    -webkit-transform: translateY(10vmin) scale(0.5,0.5);
            transform: translateY(10vmin) scale(0.5,0.5);
  }

  25% {
    -webkit-transform: translateY(10vmin) scale(3,3);
            transform: translateY(10vmin) scale(3,3);
  }

  50% {
    -webkit-transform: translateY(10vmin) scale(3,3);
            transform: translateY(10vmin) scale(3,3);
  }

  100% {
    -webkit-transform: translateY(0) scale(1,1);
            transform: translateY(0) scale(1,1);
  }
}

@-webkit-keyframes Fade-in {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 0;
  }

  100%{
    opacity: 100%;
  }
}

@keyframes Fade-in {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 0;
  }

  100%{
    opacity: 100%;
  }
}
.Card-container{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 20vmax;
    min-width: 215px;
    margin: 5vmin 2.5vmin;
}

.Card-image{
    width: 100%;
}

.Card-header{
    text-align: left;
    width: 100%;
    min-font-size: 25px;
    font-size: 2.5vh;
    letter-spacing: 0.3vh;
    font-family: "Open Sans", serif;
    color: #FBDDFB;
}

.Card-description{
    margin: 0;
    text-align: left;
    width: 100%;
    font-size: 1.5vh;
    font-family: "Open Sans Light", serif;
    color: rgba(255,255,255,0.7);
}
.Octagon-button{
    position:relative;
    cursor: pointer;
    background-position: center;
    margin-top: 5vh;
    margin-bottom: 1vh;
}

.Octagon-background{
    fill:#FBDDFB;
    width: 15vmax;
    min-width: 215px;
    height: 100%;
    display: block;
}

.Text{
    -webkit-user-select: none; /* Safari */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
    left:0;
    top: 25%;
    width: 100%;
    margin: 0;
    text-align: center;
    position: absolute;
    z-index: 1;
    font-size: 18px;
    letter-spacing: 0.3vh;
    font-family: "Open Sans", serif;
    color: #FBDDFB;
}
.Selector-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;
    padding-top: 8vh;
}

.Selector-item {
    -webkit-user-select: none; /* Safari */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
    text-align: center;
    letter-spacing: 0.3vh;
    cursor: pointer;
    margin: 0;
    font-size: calc(10px + 2vmin);
    font-family: "Open Sans", serif;
    color: #FBDDFB;
}

.Selector-item.unselected {
    color: white;
    opacity: 0.5;
}

.Selector-divider {
    height: calc(10px + 4vmin);
    margin-left: 1vw;
    margin-right: 1vw;
    -webkit-user-select: none; /* Safari */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
}
